html {
  box-sizing: border-box;
}

*, ::after, ::before {
  box-sizing: inherit;
}

#root {
  font-family: 'Montserrat', sans-serif;
  min-height: 100vh;
  background-image: url(/currency-converter-react/static/media/money.e89a0deb.webp);
  display: flex;
  align-items: center;
  justify-content: center;
  background-position: center;
  background-size: cover;
}
.app {
    flex-basis: 700px;
    margin: 20px;
    padding: 40px;
    background: white;
    border-radius: 20px;
    box-shadow: 0px 0px 30px 0px #000000;
}
.form__labelText {
    max-width: 200px;
    width: 100%;
    display: inline-block;
    margin-left: 5px;
}

.form__field {
    border: 1px solid #aaa;
    border-radius: 10px;
    padding: 10px;
    max-width: 300px;
    width: 100%;
}

.form__fieldset {
    width: 100%;
    border: 1px solid #aaa;
    border-radius: 10px;
    padding: 20px;
}

.form__legend {
    text-align: center;
    color: teal;
    margin-bottom: 40px;
}

.form__button {
    width: 100%;
    background-color: teal;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px;
    margin: 10px 0;
    transition: 1s;
}

.form__button:hover {
    background-color: hsl(180, 100%, 30%);
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}

.form__button:active {
    background-color: hsl(180, 100%, 35%);
}

.form__info {
    font-size: 15px;
    color: grey;
    text-align: center;
}
.result {
    font-size: 20px;
    padding: 10px;
    margin-left: 5px;
    color: teal;
    text-align: center;
}
